.login-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.6);
  display: flex;
  justify-content: center;
  align-items: center;
}

.login-box {
  background-color: white;
  padding: 25px;
  border-radius: 10px;
  width: 300px;
  text-align: center;
}

.login-box input {
  display: block;
  width: 90%;
  padding: 10px;
  margin: 10px auto;
}

.login-box button {
  padding: 10px 20px;
  margin: 5px;
  background-color: #28a745;
  border: none;
  color: white;
  cursor: pointer;
}

.login-box .close-btn {
  background-color: #dc3545;
}
