.App {
  text-align: center;
  padding: 20px;
  background: #f4f4f4;
}

.product-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.product-card {
  border: 1px solid #ccc;
  background: white;
  margin: 10px;
  padding: 10px;
  width: 180px;
  box-shadow: 2px 2px 10px #aaa;
}

.product-img {
  width: 100%;
  height: 180px;
  object-fit: cover;
}

.auth-form input {
  display: block;
  margin: 10px auto;
  padding: 8px;
}

.cart-section {
  background: white;
  margin: 20px auto;
  padding: 20px;
  max-width: 500px;
  border: 1px solid #ccc;
}
